.category {
  display: block;
}

.category__list {
  @include list-style-none;
}

.category__item {
  background-color: #f2f2f2;
  margin-bottom: 20px;
  padding: 30px 21px 9px 20px;
}

.category__wrapper {
  border-bottom: 1px solid #e6e6e6;
}

.category__title {
  @include title-style-none;
  text-transform: uppercase;
  font-size: 24px;
  line-height: 37px;
  text-align: center;
  padding-bottom: 29px;
}

.category__slim {
  margin-left: 24px;
}

.category__set {
  margin-left: 51px;
}

.category__desc {
  font-family: "Arial",sans-serif;
  font-size: 14px;
  line-height: 18px;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  padding-bottom: 22px;
}

.category__link {
  font-size: 16px;
  padding: 10px 10px 10px 0;
  margin-top: 10px;
  justify-content: left;
}


@media (min-width: $tablet-min-weight) {
  .category__item {
    padding-top: 82px;
    padding-left: 53px;
    padding-bottom: 62px;
  }

  .category__wrapper {
    border: none;
  }

  .category__title {
    text-align: left;
    font-size: 36px;
    line-height: 37px;
  }

  .category__slim {
    margin-left: 0px;
  }

  .category__set {
    margin-left: 0px;
  }

  .category__desc {
    display: block;
    width: 373px;
    font-size: 16px;
    line-height: 24px;
  }

  .category__link {
    font-size: 20px;
    margin-top: 67px;
  }
}
