.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  margin: 0 auto;
  min-width: 320px;
  max-width: 500px;
  box-sizing: border-box;
  padding-right: 20px;
  padding-left: 20px;
}

body {
  position: relative;
}

@media (min-width: $tablet-min-weight) {

  .container {
    min-width: 768px;
    max-width: 800px;
    padding-right: 30px;
    padding-left: 30px;
  }

}

@media (min-width: $desctop-min-weight) {
  .container {
    min-width: 1440px;
    max-width: 1500px;
    padding-right: 110px;
    padding-left: 110px;
  }
}
